<template>
	<view class="tarbar">
		<!-- <u-tabbar activeColor="#ea9518" :value="value1" @change="change1" :safeAreaInsetBottom="true">
			<u-tabbar-item text="首页">
				<image class="u-page__item__slot-icon" slot="active-icon" src="../static/tarbar/home_on.png"
					mode="widthFix"></image>
				<image class="u-page__item__slot-icon" slot="inactive-icon" src="../static/tarbar/home.png"
					mode="widthFix"></image>
			</u-tabbar-item>
			<u-tabbar-item text="订单">
				<image class="u-page__item__slot-icon" slot="active-icon" src="../static/tarbar/order_on.png"
					mode="widthFix"></image>
				<image class="u-page__item__slot-icon" slot="inactive-icon" src="../static/tarbar/order.png"
					mode="widthFix"></image>
			</u-tabbar-item>
			<u-tabbar-item text="我的">
				<image class="u-page__item__slot-icon" slot="active-icon" src="../static/tarbar/my_on.png"
					mode="widthFix"></image>
				<image class="u-page__item__slot-icon" slot="inactive-icon" src="../static/tarbar/my.png"
					mode="widthFix"></image>
			</u-tabbar-item>
		</u-tabbar> -->
		<view v-for="(item,index) in tarbar" :key="index" @click="change1(index)">
			<image :class="current==index?'big':''" :src="current==index?item.url_on:item.url" mode=""></image>
			<text :class="current==index?'active':''">{{item.name}}</text>
		</view>
	</view>
</template>
<script>
	export default {
		props: {
			current: {
				type: [Number, String],
				default: 0
			},
		},
		data() {
			return {
				// value1: this.current,
				tarbar: [{
					name: '首页',
					url: '../static/tarbar/home.png',
					url_on: '../static/tarbar/home_on.png'
				}, {
					name: '订单',
					url: '../static/tarbar/order.png',
					url_on: '../static/tarbar/order_on.png'
				}, {
					name: '我的',
					url: '../static/tarbar/my.png',
					url_on: '../static/tarbar/my_on.png'
				}]
			}
		},
		methods: {
			tarbarHeight(a) {
				let tarbar = uni.createSelectorQuery().in(this).select(".tarbar");
				return tarbar
			},
			change1(name) {
				switch (name) {
					case 0:
						uni.switchTab({
							url: '/pages/index'
						})
						break;
					case 1:
						uni.switchTab({
							url: '/pages/dingdan'
						})
						break;
					case 2:
						uni.switchTab({
							url: '/pages/wode'
						})
						break;
				}
			},
		}
	}
</script>
<style scoped lang="scss">
	.tarbar {
		position: fixed;
		bottom: 0;
		display: flex;
		align-items: center;
		width: 100%;
		justify-content: space-around;
		background: white;

		padding-bottom: constant(safe-area-inset-bottom);
		padding-bottom: env(safe-area-inset-bottom);
		border-top: 1px solid #d9d9d9;

		view {
			display: flex;
			flex-flow: column;
			align-items: center;
			padding: 20rpx 0;
			width: 33%;

			image {
				width: 42rpx;
				height: 42rpx;
				margin-bottom: 4rpx;
			}

			text {
				font-size: 18rpx;
				font-weight: 500;
				color: #757575;
			}
		}
	}

	.big {
		animation: myfirst 1s;
	}

	@keyframes myfirst {
		0% {
			transform: scale(1);
		}

		50% {
			transform: scale(.8);
		}

		100% {
			transform: scale(1);
		}
	}

	.active {
		font-size: 18rpx;
		font-weight: 500;
		color: #EF7600;
	}
</style>
